/* App.css */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #121212;
  color: #f4f4f4;
  scroll-behavior: smooth;
}

.header {
  background: linear-gradient(90deg, #3a3a3a, #525252); /* Couleurs plus claires */
  color: #fff;
  text-align: center;
  padding: 2rem 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  animation: fadeIn 1s ease-in-out;
}

.nav {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.5rem;
  background: #2f2f2f;
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 10;
}

.nav a {
  text-decoration: none;
  color: #f4f4f4;
  font-weight: bold;
  font-size: 1rem;
  transition: color 0.3s, transform 0.3s;
}

.nav a:hover {
  color: #00d4ff;
  transform: scale(1.1);
}

.section {
  padding: 2rem 1rem;
  margin: 2rem 0;
  background: #1e1e1e;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  animation: slideUp 1s ease-in-out;
}

h2 {
  color: #00d4ff;
  margin-bottom: 1rem;
  border-bottom: 2px solid #007bff;
  display: inline-block;
  position: relative;
  animation: glow 2s infinite alternate;
}

.skills, .projects {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: space-between;
}

.card {
  background: #2e2e2e;
  border: 1px solid #444;
  border-radius: 12px;
  padding: 1.5rem;
  flex: 1 1 calc(30% - 1rem);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5);
}

.card h3 {
  color: #00d4ff;
  text-align: center;
}

.footer {
  background: linear-gradient(90deg, #3a3a3a, #525252); /* Couleurs plus claires */
  color: #fff;
  text-align: center;
  padding: 1.5rem 0;
  margin-top: 3rem;
}

.footer p {
  margin: 0;
  font-size: 0.9rem;
}

a {
  transition: color 0.3s ease, transform 0.3s ease;
}

a:hover {
  color: #00d4ff;
  transform: scale(1.05);
}

/* Media Queries for Responsiveness */
@media screen and (max-width: 768px) {
  .nav {
    gap: 1rem;
  }

  .nav a {
    font-size: 0.9rem;
  }

  .skills, .projects {
    flex-direction: column;
    align-items: center;
  }

  .card {
    flex: 1 1 100%;
    max-width: 100%;
  }

  .section {
    padding: 1.5rem 1rem;
  }
}

@media screen and (max-width: 480px) {
  .header {
    padding: 1.5rem 0;
  }

  .nav {
    flex-direction: column;
    gap: 0.8rem;
  }

  .nav a {
    font-size: 0.85rem;
  }

  .card {
    padding: 1rem;
  }

  h2 {
    font-size: 1.2rem;
  }
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes glow {
  from {
    text-shadow: 0 0 10px #00d4ff, 0 0 20px #007bff;
  }
  to {
    text-shadow: 0 0 20px #00d4ff, 0 0 30px #007bff;
  }
}
